export default {
	data() {
		return {
			title: '',
			answer: '',
			lib: []
		}
	},
	methods:{
		submit(){
			this.lib.push({'title':this.title,'answer':this.answer})
			uni.setStorage({
			    key: 'test',
			    data: this.lib,
			    success: function () {
			        console.log('success');
			    }
			});
		},
		goDelete(){
			uni.navigateTo({
			    url: '/pages/delete/delete'
			});
		}
	},
	onShow() {
		let self = this
		uni.getStorage({
			key: 'test',
			success: function (res) {
				console.log(res.data);
				if(res.data instanceof Array && res.data.length > 0){
					self.lib = res.data
				}
			}
		});
	}
}
